.__menu_item{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
}
.price{
    font-weight: bold;
    color: #b8380b;
}
.dotted-line-container{
    width: 20%;
    padding: 0;
    margin: 0 auto;
}
.dotted-line{
    border-style: dashed ;
    border-bottom: 0;
    border-left: 0;
    border-right: 0;
    list-style:circle;
}
.desc-styles{
    font-size: 9px;
    display: block;
    max-width: 100px;
    margin-top: 5px;
    color: #b44b24;
}
.name-styles{
    margin: 0px;
    margin-bottom:5px;
    margin-top: 10px;
    max-width: 100px;
    min-width: 70px;
    
}